.news{
    &-item{
        border-radius: 4px;
        overflow: hidden;
        &__img{
            position: relative;
            height: 100%;
            width: 100%;
            @include media(1024){
                &::before{
                    content: "";
                    display: block;
                    padding-top: 56%;
                }
            }
            &>*{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__content{
            padding: 20px;
            h2{
                font-size: 24px;
                margin-bottom: 10px;
            }
            p{
                margin-bottom: 20px;
            }
            a{
                color: $orange;
                border-bottom: 1px solid $orange;
                text-transform: uppercase;
                font-weight: 700;
            }
        }
    }
}
.single{
    width: 100%;
    max-width: 1060px;
    margin: 60px auto;
    background-color: $white;
    padding: 40px;
    border-radius: 4px;
    @include media(1024){
        padding-left: 0;
        padding-right: 0;
        border-radius: 0;
    }
    .breadscrumbs{
        margin-top: 0;
        margin-bottom: 30px;
    }
    h1{
        margin-bottom: 20px;
    }
    img{
        margin-bottom: 20px;
        border-radius: 4px;
    }
    p{
        &:not(:last-child){
            margin-bottom: 20px;
        }
    }
}