.bonuses{
    &-loop{
        &__item{
            background-color: $white;
            margin-bottom: 20px;
            border-radius: 4px;
            &-left{
                padding: 20px;
                border-right: 1px solid #e0e0e0;
                height: 100%;
                @include media(768){
                    border-right: none;
                }
                &-header{
                    margin-bottom: 20px;
                    padding-bottom: 20px;
                    border-bottom: 1px solid $grey-light;
                }
                .h3,h3{
                    font-size: 24px;
                    line-height: 1.2;
                    padding-right: 10px;
                }
            }
            &-content{
                margin-bottom: 20px;
                padding-bottom: 20px;
                border-bottom: 1px solid $grey-light;
            }
            &-footer{
                .btn{
                    height: 50px;
                    max-width: calc(50% - 10px);
                }
            }
            &-row{
                padding: 20px 0;
                border-top: 1px solid #e0e0e0;
                &:last-child{
                    padding-bottom: 0;
                    border-bottom: none;
                }
                &:first-child{
                    border-top: none;

                }
                &-title{
                    flex: 0 0 auto;
                    width: 100%;
                    max-width: 50%;
                }
                &-content{
                    flex: 0 0 auto;
                    width: 100%;
                    max-width: 50%;
                    display: flex;
                    justify-content: flex-end;
                    font-family: "Neo Sans Pro";
                    text-align: right;
                    line-height: 1.2;
                    span{
                        flex: 0 0 auto;
                        margin-left: 5px;
                        
                        &.price{
                            font-weight: 700;
                            &::before{
                                content: "$";
                            }
                        }
                        &.wager{
                            &::before{
                                content: "x";
                            }
                        }
                    }
                }
            }
            &-right{
                height: 100%;
                margin-left: -20px;
                margin-right: -1px;
                @include media(768){
                    margin-left: 0;
                    margin-right: 0;
                }
                .ratio{
                    flex: 0 0 auto;
                    width: 100%;
                    max-width: 100%;
                    height: 50%;
                    background-color: #e9e7e7;
                    border-top-right-radius: 4px;
                    overflow: hidden;
                    @include media(768){
                        border-top-left-radius: 4px;
                        margin-bottom: 20px;
                    }
                    img{
                        max-width: 80%;
                    }
                    &:before{
                        padding-top: 0;
                        height: 100%;
                        @include media(768){
                            height: initial;
                            padding-top: 56%;
                        }
                    }
                }
                &-rating{
                    flex: 1;
                    .rating{
                        font-size: 24px;
                    }
                }
                &-title{
                    .h3,h3{
                        font-size: 24px;
                        padding: 24px 0;
                    }
                }
                &-wrap{
                    border-top: 1px solid #e0e0e0;
                    padding-top: 20px;
                    ul{
                        margin: 0 -5px;
                        li{
                            flex: 0 0 auto;
                            width: 100%;
                            max-width: 50%;
                            padding: 5px;
                            a{
                                background-color: #e9e7e7;
                                border: 1px solid #e0e0e0;
                                display: block;
                                padding: 10px 0;
                                img{
                                    height: 30px;
                                    margin: 0 auto;
                                    padding: 0 5px;
                                }
                            }
                        }
                    }
                    
                }
            }
        }
    }
}