.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
  visibility: hidden;
  &-initialized{
    visibility: visible;
  }
}

.swiper-vertical>.swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-pointer-events {
  touch-action: pan-y;

  &.swiper-vertical {
    touch-action: pan-x;
  }
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-autoheight {

  &,
  .swiper-slide {
    height: auto;
  }

  .swiper-wrapper {
    align-items: flex-start;
    transition-property: transform, height;
  }
}
.slider{
  &-tabs{
    button{
      border: none;
      
      background-color: transparent;
      font-weight: 400;
      font-size: 1rem;
      font-family: inherit;
      margin-right: 20px;
      padding: 0;
      cursor: pointer;
      border-bottom: 2px solid transparent;
      transition: border-color .2s ease-in-out;
      &.active{
        border-bottom: 2px solid orange;
        cursor: default;
      }
    }
  }
  &-nav{
    border: 1px solid $grey-light;
    border-radius: 4px;
    position: relative;
    &:after{
      content: "";
      position: absolute;
      left: 50%;
      width: 1px;
      height: 100%;
      border-left: 1px solid $grey-light;
    }
    button{
      border: none;
      padding: 0;
      flex: 0 0 auto;
      width: 30px;
      height: 30px;
      border-radius: 3px;
      color: $Charcoal;
      cursor: pointer;
      background-color: transparent;

      &.swiper-button-disabled{
        pointer-events: none;
      }
      &.swiper-button-lock{
        display: none !important;
      }
      &:hover{
        color: $orange;
      }
      .icon{
        fill: currentColor;
        transition: color .2s ease-in-out;
      }
    }
  }
}