.comments{
    &-list{
        list-style: none;
        >li{
            &:not(:last-child){
                margin-bottom: 20px;
            }
        }
    }
}
.comment{
    &-body{
        background-color: $white;
        border-radius: 4px;
        padding: 20px;
        display: flex;
        flex-wrap: wrap;
        &__left{
            flex: 0 0 auto;
            width: 200px;
            max-width: 200px;
            display: flex;
            flex-direction: column;
            @include media(768){
                width: 100%;
                max-width: 100%;
            }
            &-img{
                flex: 0 0 auto;
                width: 100px;
                max-width: 100px;
                margin-bottom: 10px;
                &:before{
                    padding-top: 100%;
                }
                &>*{
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    border-radius: 100%;
                    background-color: $GoldenYellow;
                    overflow: hidden;
                    font-size: 40px;
                    font-weight: 700;
                }
            }
            &-name{
                margin-bottom: 10px;
                span{
                    font-weight: 700;
                    font-size: 20px;
                }
            }
            &-country{
                //margin-bottom: 10px;
                .icon-country{
                    flex: 0 0 auto;
                    width: 14px;
                    max-width: 14px;
                    margin-left: auto;
                    margin-right: auto;
                    img{
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                    }
                }
                .country-name{
                    font-weight: 700;
                    font-size: 14px;
                    color: $grey;
                    text-align: center;
                    margin-left: 5px;
                    margin-right: 5px;
                    line-height: 1.3;
                }
            }
            .rating{
                font-size: 24px;
            }
        }
        &__right{
            flex: 1;
            padding: 20px 0;
            &-meta{
                border-bottom: 1px solid #e0e0e0;
                margin-bottom: 20px;
                &-header{
                    margin-bottom: 10px;
                    &-rating{
                        @include media(768){
                            justify-content: center !important;
                            width: 100%;
                        }
                    }
                    .date{
                        font-size: 14px;
                        font-weight: 700;
                        color: $grey;
                        position: relative;
                        top: 2px;
                        margin-right: 10px;
                    }
                    a{
                        display: block;
                        margin-left: 10px;
                        height: 30px;
                        background-color: #e9e7e7;
                        padding: 5px 10px;
                        border-radius: 3px;
                        overflow: hidden;
                        img{
                            height: 100%;
                        }
                    }
                    .rating{
                        font-size: 24px;
                        margin-left: -2px;
                    }
                    ul{
                        @include media(1024){
                            margin-top: 20px;
                            width: 100%;
                            justify-content: flex-start;
                        }
                        li{
                            margin-left: 20px;
                            @include media(1024){
                                margin-left: 0;
                                margin-right: 10px;
                            }
                            .icon-lang{
                                flex: 0 0 auto;
                                width: 16px;
                                max-width: 16px;
                                margin-right: 5px;
                                img{
                                    top: 0;
                                }
                            }
                            span{
                                font-weight: 700;
                            }
                        }
                    }
                }
                &-footer{
                    padding-bottom: 10px;
                    .date{
                        color: $grey;
                    }
                }
            }
            &-text{
                position: relative;
                max-height: 72px;
                overflow: hidden;
                position: relative;
                &.full{
                    max-height: initial;
                    &:after{
                        display: none;
                    }
                }
                &.opened{
                    padding-bottom: 20px;
                    .show-more{
                        &:before{
                            display: none;
                        }
                    }
                }
                
                .show-more{
                    font-size: 12px;
                    font-weight: 700;
                    text-transform: uppercase;
                    color: $orange;
                    display: inline-block;
                    cursor: pointer;
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    background: #fff;
                    z-index: 2;
                    transition: 0.5s;
                    padding: 5px 0;
                    &:after{
                        content: "";
                        border-bottom: 1px solid $orange;
                        width: 100%;
                        position: absolute;
                        left: 0;
                        bottom: 0;
                    }
                    &:before{
                        content: " ";
                        box-shadow: -18px 0 20px 11px rgb(255 255 255 / 88%);
                        background: #fff;
                        bottom: auto;
                        right: 0;
                        position: absolute;
                        width: calc(100% + 15px);
                        height: 100%;
                        transition: .5s;
                        top: 0;
                        border-radius: 15px;
                        z-index: -1;
                    }
                    
                }
                &-item{
                    display: none;
                    &.active{
                        display: block;
                    }
                }
            }
        }
    }
}