.popup{
    padding: 40px;
    border-radius: 4px;
    min-width: 500px;
    @include media(768){
        min-width: initial;
        padding-left: 20px;
        padding-right: 20px;
        width: 100%;
    }
    &-title{
        font-size: 24px;
        font-weight: 700;
        color: $Charcoal;
    }
}
.fancybox__content>.carousel__button.is-close {
    position: absolute;
    top: 10px;
    right: 10px;
    color: $Charcoal;
}
.carousel__button svg{
    filter: none;
}
.fancybox__backdrop{
    background: rgba(0, 123, 255, .9);
}