.home {
    padding-top: 60px;
    &-article{
        p{
            font-size: 16px;
            &:not(:last-child){
                margin-bottom: 20px;
            }
        }
    }
    .h2,
    h2 {
        margin-bottom: 20px;
    }
    &-blocks{
        &__footer{
            .btn{
                max-width: 200px;
            }
        }
    }

    &-bonuses {
        padding: 60px 0;
        margin-bottom: 60px;
        background-color: $white;

        .title {
            margin-bottom: 30px;
        }

        h2 {
            font-size: 26px;
        }

        .col-4-xl {
            margin: 10px 0;
        }
        &__wrap{
            margin-bottom: 30px;
            a {
                color: #007bff;
                transition: box-shadow .3s ease-in-out;
                padding: 0;
                &::before{
                    display: none
                }
    
                &:hover {
                    box-shadow: 0 5px 10px rgba(0, 0, 0, .06);
                }
                .csn-right__blocks-item-text{
                    padding: 20px;
                    div{
                        line-height: 1.3;
                    }
                    p{
                        color: $Charcoal;
                        margin-top: 5px;
                        font-weight: 700;
                    }
                }
            }
        }

        span {
            font-size: 16px;
            color: #1a1a1a;
            display: block;
            margin-top: 10px;
            line-height: 1.5;
            -webkit-line-clamp: 2;
            display: box;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            text-overflow: ellipsis;
            white-space: normal;
            overflow: hidden;
        }
    }

    &-wrapper {
        padding: 0 0 60px;

        .title {
            margin-bottom: 20px;
        }
    }

    &-about {
        &__wrap {
            color: #fff;
            background: linear-gradient(135deg, #007bff 30%, #6f42c1);
            border-radius: 4px;
            overflow: hidden;
            margin-bottom: 60px;
        }

        &__description {
            padding: 60px;
            @include media(1280){
                padding-left: 40px;
                padding-right: 40px;
            }
            @include media(1024){
               padding-bottom: 20px;
               padding-top: 20px;
            }

            h1 {
                font-size: 32px;
                margin-bottom: 20px;
                @include media(1024){
                    text-align: center;
                }
            }
            p{
                @include media(1024){
                    text-align: center;
                }
            }
        }

        img {
            max-width: 80%;
        }

        &__numbers {
            position: relative;
            @include media(1280){
                margin: 0 40px;
            }
            @include media(768){
                margin: 0 20px;
            }

            &-wrap {
                padding-top: 30px;
                padding-bottom: 30px;
                @include media(1280){
                    padding-top: 0;
                    padding-bottom: 20px;
                }
                
            }

            &:before {
                content: "";
                position: absolute;
                width: 100%;
                height: 1px;
                border-bottom: 1px solid rgba(255, 255, 255, .33);
            }

            &:after {
                content: "";
                position: absolute;
                width: 1px;
                height: 100%;
                border-left: 1px solid rgba(255, 255, 255, .33);
            }

            &-item {
                flex: 0 0 auto;
                width: 50%;
                max-width: 50%;
                padding: 20px;
                color: $white;

                &-header {
                    margin-bottom: 10px;

                    span {
                        font-weight: 700;
                    }

                    .icon {
                        width: 24px;
                        max-width: 24px;
                        height: 24px;
                        display: block;
                        margin-bottom: 10px;

                    }
                }

                &-footer {
                    span {
                        font-size: 32px;
                        font-family: "Neo Sans Pro";
                        font-weight: 700;
                    }
                }
            }
        }
    }

    &-csn {
        .csn-loop__item {
            margin-bottom: 0;
        }
        .sliders{
            margin-bottom: 40px;
        }
    }

    &-games {
        .row{
            margin-bottom: 20px;
        }
        &__category {
            margin-bottom: 40px;
            position: relative;
            @include media(1024){
                overflow: hidden;
            }
            

            &:hover {
                .home-games__category-btn {
                    transform: none;
                    opacity: 1;
                }
            }

            &-btn {
                position: absolute;
                z-index: 2;
                height: 100%;
                background-color: gold;
                border: none;
                cursor: pointer;

                &-prev {
                    left: 0;
                    transform: translateX(-20px);
                    opacity: 0;
                    transition: opacity .3s ease-in-out, transform .3s ease-in-out;
                    &.swiper-button-disabled {
                        transform: translateX(-20px) !important;
                        opacity: 0 !important;
                        pointer-events: none;
                    }
                }

                &-next {
                    right: 0;
                    transform: translateX(20px);
                    opacity: 0;
                    transition: opacity .3s ease-in-out, transform .3s ease-in-out;

                    &.swiper-button-disabled {
                        transform: translateX(20px) !important;
                        opacity: 0 !important;
                        pointer-events: none;
                    }
                }
            }
        }

        .load-more {
            max-width: 200px;
            margin-top: 20px;
        }
    }
}