.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 10px;
  @include media(1280){
    max-width: 980px;
  }
  @include media(1024){
    max-width: 100%;
  }
}

.row {
  margin-left: -10px;
  margin-right: -10px;
  display: flex;
  flex-wrap: wrap;

  >* {
    padding: 0 10px;
    width: 100%;
    max-width: 100%;
  }
}


[class^=col] {
  -webkit-box-flex: 1;
  -ms-flex: 1 0 0%;
  flex: 1 0 0%;
}

[class^=col-] {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
}


@include create-cols(2, 12, xl);
@include create-cols(3, 12, xl);
@include create-cols(4, 12, xl);
@include create-cols(5, 12, xl);
@include create-cols(6, 12, xl);
@include create-cols(7, 12, xl);
@include create-cols(8, 12, xl);
@include create-cols(9, 12, xl);
@include create-cols(10, 12, xl);
@include create-cols(11, 12, xl);
@include create-cols(12, 12, xl);

@media only screen and (max-width: 1280px) {
  @include create-cols(6, 12, lg);
  @include create-cols(12, 12, lg);
}
@media only screen and (max-width: 1023.98px) {
  @include create-cols(6, 12, md);
  @include create-cols(12, 12, md);
}
@media only screen and (max-width: 767.98px) {
  @include create-cols(6, 12, sm);
  @include create-cols(12, 12, sm);
}
.main-wrapper{
  flex: 1;
}