* {
    box-sizing: border-box;
    outline: none !important;
}

html {
    font-size: 16px;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    text-size-adjust: none;
}

body {
    font-size: 1rem;
    //font-family: 'Neo Sans Pro';
   // font-family: "Noto Sans", sans-serif;
    font-family: "Poppins", "Noto Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-variation-settings: "wdth" 100;
    background-color: $body;
    color: $Charcoal;
    &.lock{
        overflow: hidden;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 0;
    margin-bottom: 0;
    font-size: inherit;
    font-weight: inherit;
}

ul{
    margin-top: 0;
    padding: 0;
    list-style: none;
}
ol{
    margin-top: 0;
    padding: 0;
}

p {
    margin-top: 0;
    line-height: 1.5;
}

a {
    text-decoration: none;
    color: $link;
}

button,
input {
    font-family: inherit;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

input,
textarea {
    &::-webkit-input-placeholder {
        opacity: 1;
        transition: opacity 0.3s ease;
    }

    &:focus {
        &::-webkit-input-placeholder {
            opacity: 0;
        }
    }
}

input,
textarea {
    &::-moz-placeholder {
        opacity: 1;
        transition: opacity 0.3s ease;
    }

    &:focus {
        &::-moz-placeholder {
            opacity: 0;
        }
    }
}


