.d-flex{
    display: flex;
}
.d-i-flex{
    display: inline-flex;
}
.flex-wrap{
    flex-wrap: wrap;
}
.nowrap{
    flex-wrap: nowrap;
}
.flex-column{
    flex-direction: column;
}
.justify-start{
    justify-content: flex-start;
}
.justify-end{
    justify-content: flex-end;
}
.justify-center{
    justify-content: center;
}
.justify-between{
    justify-content: space-between;
}
.justify-around{
    justify-content: space-around;
}
.row-reverse{
    flex-direction: row-reverse;
}
.column-reverse{
    flex-direction: column-reverse;
}
.align-center{
    align-items: center;
}
.align-start{
    align-items: flex-start;
}
@include media(1024){
    .column-reverse-md{
        flex-direction: column-reverse;
    }
    .column-md{
        flex-direction: column;
    }
}
@include media(768){
    .column-reverse-sm{
        flex-direction: column-reverse;
    }

}