.hero {
    padding: 0 0 60px;
    //background: linear-gradient(45deg, #1e90ff, #00bfff);
    //background-size: 400% 400%;

    color: $white;
    background: linear-gradient(135deg, #007BFF 30%, #6F42C1);

    h1 {
        margin-bottom: 15px;
        overflow: hidden;
        display: block;
        -webkit-line-clamp: 2;
        display: box;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        text-overflow: ellipsis;
        white-space: normal;
    }

    &__img {
        flex: 0 0 auto;
        width: 100%;

        &::before {
            width: 100%;
            padding-top: 33%;
        }

        img {
            height: 100%;
        }
        .baner{
            font-size: 160px;
            @include media(1280){
                font-size: 70px;
            }
            @include media(768){
                font-size: 62px;
            }
        }
    }
    .col-6-xl{
        position: relative;
        @include media(768){
            align-items: center;
        }
    }
    &__rating{
        cursor: pointer;
        @include media(768){
            text-align: center;
        }
        &:hover{
            .rating-full{
                opacity: 1;
                transform: none;
            }
        }
    }
}

.breadscrumbs {
    margin-top: 20px;
    margin-bottom: 50px;

    ul {
        li {
            a {
                font-size: 12px;
                color: $white;
                margin-right: 5px;
                display: flex;
                align-items: center;

                .icon {
                    flex: 0 0 auto;
                    width: 8px;
                    max-width: 8px;
                    height: 8px;
                    margin-left: 5px;
                }
            }

            span {
                font-size: 12px;
                color: $white;
                display: flex;
                align-items: center;
                .icon {
                    flex: 0 0 auto;
                    width: 8px;
                    max-width: 8px;
                    height: 8px;
                    display: none;
                }
            }
        }
    }
    &-dark{
        ul{
            li{
                a,span{
                    color: $orange;
                }
                span{
                    color: $Charcoal;
                }
            }
        }
    }
}

@keyframes gradientAnimation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

@keyframes flyAnimation {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-20px);
    }

    100% {
        transform: translateY(0);
    }
}