.faq{
    &-item{
        border-radius: 4px;
        padding: 20px 20px 0;
        &:not(:last-child){
            margin-bottom: 20px;
        }
        .accordion__item-icon{
            span{
                width: 24px;
                max-width: 24px;
                height: 24px;
                svg{
                    width: 24px;
                    height: 2px;
                }
            }
        }
    }
    &-wrap{
        p{
            margin-bottom: 20px;
        }
    }
    &-title{
        padding-bottom: 20px;
        cursor: pointer;
        .title{
            font-weight: 700;
            line-height: 1.4;
            flex: 1;
            font-size: 20px;
            max-width: calc(100% - 24px);
            @include media(768){
                font-size: 16px;
            }
        }
    }
}