.section{
    padding: 60px 0;
}
.mb-15{
    margin-bottom: 15px;
}
.mb-20{
    margin-bottom: 20px;
}
.mt-20{
    margin-top: 20px;
}
.mb-0{
    margin-bottom: 0;
}
@include media(1024){
    .mb-20-md{
        margin-bottom: 20px;
    }
    .mb-60-md{
        margin-bottom: 60px;
    }
}
@include media(768){
    .mb-20-sm{
        margin-bottom: 20px;
    }
}