.hero{
    &-game{
        .breadscrumbs{
            margin-bottom: 20px;
        }
    }
}
.game {
    &-title{
        h1{
            margin-bottom: 0;
        }
        button{
            background-color: transparent;
            padding: 0;
            border: none;
            width: 30px;
            max-width: 30px;
            height: 30px;
            flex: 0 0 auto;
            color: $white;
            position: relative;
            top: -5px;
            cursor: pointer;
            opacity: 0;
            pointer-events: none;
            &.active{
                opacity: 1;
                pointer-events: all;
            }
            .icon{
                width: 100%;
                height: 100%;
            }
        }
    }
    &-overlay {
        position: relative;
        width: 100%;
        border-radius: 4px;
        overflow: hidden;
        margin-bottom: 20px;

        &.active{
            .game-overlay__desc{
                display: none;
            }
        }

        &__video {
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            iframe{
                width: 100%;
                height: 100%;
                position: absolute;
            }
        }
        &__desc{
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            &-btns{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(111, 66, 193, .5);
                backdrop-filter: blur(3px);
                button{
                    margin: 10px 0;
                    flex: 0 0 auto;
                    max-width: 300px;
                    &.btn{
                        &-primary{
                            .icon{
                                stroke-width: 2;
                                margin-right: 5px;
                                display: block;
                                position: relative;
                                top: 1px;
                            }
                        }
                        &-secondary{
                            .icon{
                                width: 24px;
                                max-width: 24px;
                                height: 20px;
                                transform: none !important;
                                display: block;
                                margin-right: 5px;
                                margin-left: 0;
                                position: relative;
                                top: 1px;
                            }
                            
                        }
                    }
                }
            }
        }
    }
    &-csn{
        &__title{
            margin-bottom: 40px;
        }
    }
    &-right{
        &__desc{
            margin-bottom: 60px;
            h2{
                font-size: 24px;
                margin-bottom: 20px;
            }
            p{
                &:not(:last-child){
                    margin-bottom: 20px;
                }
            }
        }
        &__video{
            margin-bottom: 60px;
            h2{
                font-size: 24px;
                margin-bottom: 20px;
            }
            .ratio{
                border-radius: 4px;
                overflow: hidden;
            }
            iframe{
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
        &__screenshots{
            h2{
                font-size: 24px;
            }
            .title{
                margin-bottom: 20px;
            }
        }
    }
}