.rating {
    font-family: star !important;
    position: relative;
    overflow: hidden;
    width: 5em;

    &:before {
        content: "\e900\e900\e900\e900\e900";
        color: #dad3cc;
    }

    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;

        &:before {
            content: "\e900\e900\e900\e900\e900";
            color: orange;
        }
    }

    &-count {
        font-size: 24px;
        font-family: 'Neo Sans Pro';
        margin-bottom: 5px;

        span {
            font-size: 32px;
            font-weight: 700;
        }
    }
    &-full{
        /* position: absolute;
        z-index: 1;
        top: calc(100% + 10px);
        pointer-events: none;
        transform: translateY(20px);
        opacity: 0;
        transition: opacity .3s ease-in-out, transform .3s ease-in-out; */
        &__wrap{
            background-color: $white;
            box-shadow: 0 5px 10px rgba(0, 0, 0, .06);
            padding: 30px;
            @include media(768){
                padding: 20px;
            }
        }
        &__item{
            color: $Charcoal;
            &:not(:last-child){
                margin-bottom: 20px;
            }
            &-title{
                font-weight: 500;
            }
            .rating{
                font-size: 22px;
                height: initial;
                margin-left: 5px;
            }
        }
    }
}

.badge {
    display: inline-block;
    padding: 10px 20px;
    font-size: 12px;
    color: #fff;
    border-radius: 4px;
    font-weight: bold;
    text-transform: uppercase;
}

.badge.exclusive {
    background: linear-gradient(90deg, #FFD700, #FFA500);
    color: $Charcoal;
}

.badge.top {
    background: linear-gradient(90deg, #FF3B30, #FF6F61);
}

.badge.recommended {
    background: linear-gradient(90deg, #1E90FF, #4FC3F7);
}

.badge.new {
    background: linear-gradient(90deg, #2ECC71, rgb(113, 255, 129));
}

.comments {
    &-rating {
        &__container {
            font-size: 0;
            unicode-bidi: bidi-override;
            direction: rtl;

            &>input {
                display: none;
                &:checked{
                    &~label{
                        &::before{
                            color: orange;
                        }
                    }
                }

                +label {
                    display: inline-block;
                    cursor: pointer;
                    font-family: star !important;
                    &:hover{
                        &:before{
                            color: orange;
                        }
                        &~label{
                            &::before{
                                color: orange;
                            }
                        }
                    }

                    &:before {
                        content: "\e900";
                        color: #dad3cc;
                        font-size: 24px;
                        transition: color .2s ease-in-out;
                    }
                }
            }
        }
    }
}