.btn {
    text-transform: uppercase;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    width: 100%;
    border-radius: 4px;
    overflow: hidden;
    color: $Charcoal;
    font-size: initial;
    border: none;
    cursor: pointer;

    &-primary {
        background-color: $GoldenYellow;
        transition: box-shadow .3s ease-in-out !important;

        &:hover {
            box-shadow: 0 0 12px rgba(255, 215, 0, 0.6);
            color: $Charcoal !important;
        }
    }

    &-secondary {
        background-color: $purple;
        color: $white;
        transition: box-shadow .3s ease-in-out;

        .icon {
            flex: 0 0 auto;
            width: 24px;
            max-width: 24px;
            height: 24px;
            transition: transform .2s ease-in-out;
            transform: none;
            will-change: transform;
        }

        &:hover {
            box-shadow: 0 0 12px rgba(111, 66, 193, 0.6);

            .icon {
                transform: translateX(5px);
            }
        }
    }

    &-burger {
        display: none;
        padding: 0;
        border: none;
        border-radius: 0;
        background-color: transparent;
        outline: none;
        position: relative;
        width: 20px;
        height: 100%;
        overflow: hidden;
        cursor: pointer;
        &.active{
            .btn-burger__line{
                &:first-child {
                    opacity: 0;
                    transform: translate(-100%, -50%);
                }
                &:nth-child(2) {
                    transform: translate(-50%, -50%) rotate(45deg);
                    &::after{
                        transform: translate(-50%, -50%) rotate(90deg);

                    }
                }
                &:last-child {
                    opacity: 0;
                    transform: translate(100%, -50%);
                }
            }
        }

        &__line {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            display: block;
            width: 20px;
            height: 2px;
            border-radius: 2px;
            background: $white;
            transition: transform 0.3s, opacity 0.2s;

            &:first-child {
                top: calc(50% - 6px);
            }

            &:nth-child(2){
                &::after{
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    display: block;
                    width: 20px;
                    height: 2px;
                    border-radius: 2px;
                    background: $white;
                    transition: transform 0.3s, opacity 0.2s;
                    content: "";
                }
            }

            &:last-child {
                top: auto;
                bottom: calc(50% - 8px);
            }
        }

        @include media(1024) {
            display: block;

        }
    }
    &-filter{
        display: none;
        border: 1px solid #5e7279;
        color: #5e7279;
        font-weight: 700;
        padding: 0 10px;
        line-height: 1;
        height: 100%;
        .icon{
            margin-right: 5px;
        }
        @include media(1280){
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}