@font-face {
    font-family: 'Neo Sans Pro';
    src: local('Neo Sans Pro Regular'), local('Neo-Sans-Pro-Regular'),
        url('../fonts/neo/NeoSansProCyr-Regular.woff2') format('woff2');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'Neo Sans Pro';
    src: local('Neo Sans Pro Medium'), local('Neo-Sans-Pro-Medium'),
        url('../fonts/neo/NeoSansProCyr-Medium.woff2') format('woff2');
    font-weight: 500;
    font-style: normal;
  }
@font-face {
    font-family: 'Neo Sans Pro';
    src: local('Neo Sans Pro Bold'), local('Neo-Sans-Pro-Bold'),
        url('../fonts/neo/NeoSansProCyr-Bold.woff2') format('woff2');
    font-weight: 700;
    font-style: normal;
  }
  @font-face {
    font-family: 'Satoshi';
    src: local('Satoshi Regular'), local('Satoshi-Regular'),
      url('../fonts/Satoshi-Regular/Satoshi-Regular.woff2') format('woff2');
    font-weight: 400;
    font-style: normal;
}
  @font-face {
    font-family: 'Satoshi';
    src: local('Satoshi Bold'), local('Satoshi-Bold'),
      url('../fonts/Satoshi-Bold/Satoshi-Bold.woff2') format('woff2');
    font-weight: 700;
    font-style: normal;
}
  @font-face {
	font-family: '#{$icomoon-font-family}';
	src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?8i661s');
	src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?8i661s#iefix') format('embedded-opentype'),
	  url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?8i661s') format('truetype'),
	  url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?8i661s') format('woff'),
	  url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?8i661s##{$icomoon-font-family}') format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: block;
  }
h1,.h1{
    font-size: 42px;
    line-height: 1.4;
    font-weight: 700;
    @include media(768){
      font-size: 28px;
    }
}
h2,.h2{
    font-size: 32px;
    line-height: 1.4;
    font-weight: 700;
    @include media(768){
      font-size: 24px;
    }
}
.text-font-secondary{
    //font-family: 'Neo Sans Pro';
    font-family: "Poppins", sans-serif;
}
.text-upper{
    text-transform: uppercase;
}
.text-bold{
    font-weight: 700;
}