.csn {
    &-logo{
        background-color: #e9e7e7;
        border: 1px solid #e0e0e0;
        padding: 0 10px;
    }
    &-filter {
        position: sticky;
        top: 130px;
        @include media(1280){
            position: fixed;
            left: 0;
            top: 125px;
            width: 100%;
            background-color: #fff;
            height: 100vh;
            height: calc(var(--vh, 1vh) * 100 - 125px);
            z-index: 100;
            overflow: auto;
            transition: transform .3s;
            transform: translateX(-100%);
            pointer-events: none;
            &.active{
                transform: none;
                pointer-events: all;
            }
            @include media(1024){
                height: calc(var(--vh, 1vh) * 100 - 59px);
                top: 59px;
            }
        }

        &__title {
            margin-bottom: 20px;
            font-size: 24px;
            @include media(1280){
                padding: 20px 20px 0;
            }
        }
    }

    &-reviews {
        &__wrap{
            display: flex;
            flex-direction: column-reverse;
        }
        &__header{
            height: 100%;
            margin-bottom: 60px;
            @include media(768){
                height: initial;
            }
            h2 {
                margin-bottom: 10px;
                font-size: 52px;
                position: relative;
                @include media(1280){
                    font-size: 32px;
                }
                span{
                    font-size: 16px;
                    position: absolute;
                    background: linear-gradient(90deg, gold, orange);
                    padding: 3px 10px;
                    border-radius: 4px;
                }
            }
            button{
                flex: 0 0 auto;
                width: 300px;
            }
        }
    }

    &-left {
        border: 1px solid $grey-light;
        padding: 15px;
        position: sticky;
        top: 130px;
        max-width: calc(100% - 40px);
        @include media(1280){
            max-width: initial;
        }

        &__item {
            padding: 15px 0;

            &:not(:last-child) {
                border-bottom: 1px solid $grey-light;
            }

            &-title {
                font-size: 12px;
                color: $grey;
                margin-bottom: 5px;
            }

            &-wrap {
                &.img-link {
                    a {
                        flex: 0 0 auto;
                        width: calc(100% / 4 - 10px);
                        border: 1px solid $grey-light;
                        margin-right: 0;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                }

                span {
                    margin-right: 10px;
                    margin-top: 10px;
                    font-weight: 700;
                    color: $Charcoal;
                }

                a {
                    &:not(.btn){
                        margin-right: 5px;
                        margin-top: 10px;
                        font-weight: 700;
                        color: $Charcoal;
                        transition: color .2s ease-in-out;
                    }
                    

                }
            }
        }
    }

    &-right {
        &__desc {
            margin-bottom: 60px;
            h2{
                margin-bottom: 20px;
            }

            p {
                &:not(:last-child) {
                    margin-bottom: 20px;
                }
            }
        }

        &__bonuses {
            margin-bottom: 60px;

            a {
                color: $AzureBlue;
                transition: box-shadow .3s ease-in-out;

                &:hover {
                    box-shadow: 0 5px 10px rgba(0, 0, 0, .06);

                    .icon {
                        opacity: 1;
                        transform: none;
                    }
                }
            }

            .icon {
                flex: 0 0 auto;
                margin-right: 10px;
            }

            span {
                font-size: 14px;
                color: $Charcoal;
                display: block;
                margin-top: 10px;
                line-height: 1.5;
                -webkit-line-clamp: 2;
                display: box;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                text-overflow: ellipsis;
                white-space: normal;
                overflow: hidden;
            }
        }

        &__blocks {
            &-item {
                border: 1px solid #E0E0E0;
                padding: 20px;
                height: 100%;
                position: relative;
                &:before{
                    content: "";
                    width: 3px;
                    background-color: $AzureBlue;
                    position: absolute;
                    top: -1px;
                    bottom: -1px;
                    left: -1px;
                }

                &-icon {
                    margin-right: 5px;
                }
                &-img{
                    flex: 0 0 auto;
                    width: 30%;
                    max-width: 30%;
                    background-color: #e9e7e7;
                    height: 100%;
                    position: relative;
                    img{
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%,-50%);
                        max-width: 80%;
                    }
                }

            }

            &-title {
                margin-bottom: 20px;
                color: $grey;
            }

            &-wrap {
                display: grid;
                grid-template-columns: repeat(4, 1fr);
                gap: 10px;

                a {
                    border: 1px solid #e0e0e0;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    img {
                        height: 36px;
                    }
                }
            }
        }
    }

    &-order {
        height: 24px;
        margin-bottom: 20px;
        &__form{
            flex: 1;
        }

        form{
            height: 100%;
            
        }

        span {
            font-weight: 700;
            font-size: 12px;
            color: $grey;
            flex: 0 0 auto;
            margin-right: 10px;
        }

        select {
            height: 100%;
        }
    }

    &-loop {
        &__item {
            margin-bottom: 20px;
            border-radius: 4px;
            background-color: $white;

            &-left {
                margin-right: -20px;
                height: 100%;
                border-right: 1px solid $grey-light;
                @include media(768){
                    margin-right: 0;
                    border-right: none;
                    height: initial;
                }
                .ratio{
                    flex: 0 0 auto;
                    width: 100%;
                    height: 50%;
                    background-color: #e9e7e7;
                    border-top-left-radius: 4px;
                    @include media(768){
                        height: initial;
                        margin-bottom: 20px;
                    }
                    &:before{
                        padding-top: 0;
                        height: 100%;
                        @include media(768){
                            padding-top: 52%;
                            height: initial;
                        }
                    }
                    
                }

                img {
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    height: 40px;
                    max-width: 80%;
                }

                &-rating {
                    flex-direction: row-reverse;
                    flex: 1;

                    .rating-count {
                        margin-bottom: 5px;
                        font-size: 20px;

                        span {
                            font-size: 26px;
                        }
                    }
                }

                .rating {
                    font-size: 24px;
                }
            }

            &-right {
                padding: 20px 20px 20px 0;
                @include media(768){
                    padding: 0;
                }

                &-block {
                    &:not(:last-child) {
                        margin-bottom: 24px;
                    }

                    a {
                        font-size: 12px;
                        color: $orange;
                        font-family: "Neo Sans Pro";
                        font-weight: 700;
                        text-transform: uppercase;
                        transition: opacity .2s ease-in-out;
                        margin-right: 5px;

                        &:hover {
                            opacity: .8;
                        }
                    }

                    .text-font-secondary {
                        color: $grey;
                        margin-bottom: 15px;
                        font-size: 14px;
                    }

                    .csn-right__blocks-wrap {
                        gap: 5px;

                        a {
                            margin-right: 0;

                            img {
                                height: 30px;
                            }
                        }
                    }
                }
            }

            &-center {
                padding: 20px 0;
                border-right: 1px solid #e0e0e0;
                height: 100%;
                @include media(768){
                    border-right: none;
                }

                &-header {
                    margin-bottom: 20px;
                    padding-bottom: 20px;
                    padding-right: 20px;
                    padding-left: 20px;
                    border-bottom: 1px solid #e0e0e0;
                }

                .h3 {
                    font-size: 24px;
                }

                &-text {
                    margin-bottom: 20px;
                    padding-bottom: 20px;
                    padding-right: 20px;
                    padding-left: 20px;
                    border-bottom: 1px solid #e0e0e0;
                    font-size: 14px;
                }

                &-footer {
                    padding-right: 20px;
                    padding-left: 20px;

                    a {
                        height: 50px;
                        max-width: calc(50% - 10px);
                    }
                }
            }
        }


    }
}

.payment-wrap,
.providers-wrap {
    padding: 0 40px;
    @include media(768){
        padding: 0;
    }
}