// Mixin @media ( @include media(767){} )

@mixin media ($breakpoint) {
	@if $breakpoint==1920 {
		@media only screen and (max-width: 1919.98px) {
			@content;
		}
	}
	@if $breakpoint==1700 {
		@media only screen and (max-width: 1699.98px) {
			@content;
		}
	}

	@if $breakpoint==1280 {
		@media only screen and (max-width: 1280px) {
			@content;
		}
	}

	@if $breakpoint==1024 {
		@media only screen and (max-width: 1023.98px) {
			@content;
		}
	}
	@if $breakpoint==768 {
		@media only screen and (max-width: 767.98px) {
			@content;
		}
	}
}

// Mixin column

@mixin create-cols($span, $total, $breakpoint) {
    .col-#{$span}-#{$breakpoint}{
      width: ((calc($span/$total))*100%);
    }
  }

// Mixin keyframes ( @include keyframes(animation-name) {})

@mixin keyframes($name) {
	@-webkit-keyframes #{$name} {
		@content;
	}
	@-moz-keyframes #{$name} {
		@content;
	}
	@-ms-keyframes #{$name} {
		@content;
	}
	@keyframes #{$name} {
		@content;
	}
}