.pagination {
    @include media(768){
        overflow: auto;
        width: calc(100% + 20px);
        margin-left: -10px;
        margin-right: -10px;
    }
    ul {
        display: flex;
        justify-content: center;
        margin-top: 40px;
        @include media(768){
            justify-content: flex-start;
        }

        li {
            span {
                width: 50px;
                height: 50px;
                border-radius: 50%;
                background-color: #fff;
                font-size: 15px;
                text-transform: uppercase;
                margin: 0 10px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-family: "Neo Sans Pro";
                font-weight: 700;
            }

            a {
                width: 50px;
                height: 50px;
                border-radius: 50%;
                background-color: #dedbd6;
                font-size: 15px;
                text-transform: uppercase;
                margin: 0 10px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-family: "Neo Sans Pro";
                font-weight: 700;
                color: $Charcoal;
                transition: background-color .2s ease-in-out;
                &:hover{
                    background-color: $white;
                }
            }
        }
    }
}