.footer{
    background-color: $AzureBlue;
    //background: linear-gradient(180deg, #007BFF 30%, #6F42C1);
    .first-row{
        @include media(1024){
            margin-bottom: 40px;
        }
    }
    &-top{
        padding: 60px 0;
        @include media(1024){
            padding-bottom: 40px;
        }
    }
    &__logo{
        margin-bottom: 60px;
        @include media(1024){
            text-align: center;
            margin-bottom: 40px;
        }
        a{
            font-size: 30px;
            font-weight: 700;
            color: $white;
            @include media(1024){
                justify-content: center;
            }
        }
    }
    &__social{
        &-title{
            color: $white;
            margin-bottom: 15px;
            font-weight: 700;
            @include media(1024){
                text-align: center;
            }
        }
        ul{
            @include media(1024){
                justify-content: center;
              }
            li{
                &:not(:last-child){
                    margin-right: 10px;
                }
                a{
                    color: $white;
                    width: 30px;
                    height: 30px;
                    border: 1px solid $white;
                    border-radius: 100%;
                    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
                    &:hover{
                        background-color: $GoldenYellow;
                        border-color: $GoldenYellow;
                        color: $Charcoal;
                    }
                    .icon{
                        flex: 0 0 auto;
                        width: 16px;
                        max-width: 16px;
                        height: 16px;
                        display: block;
                        transition: color .2s ease-in-out;
                    }
                }
            }
        }
    }
    &__nav{
        @include media(1024){
            text-align: center;
          }
        &-title{
            margin-bottom: 15px;
            color: $white;
        }
        nav{
            ul{
                li{
                    &:not(:last-child){
                        margin-bottom: 10px;
                    }
                    a{
                        color: $white;
                        border-bottom: 1px solid transparent;
                        line-height: 1.5;
                        transition: border-color .2s ease-in-out;
                        font-size: 14px;
                        &:hover{
                            border-bottom-color: $white;
                        }
                    }
                }
            }
        }
    }
    &__bottom{
        border-top: 1px solid rgba(255, 255, 255, 0.2);
        padding: 20px 0;
        p{
            text-align: center;
            color: $white;
            font-size: 14px;
            font-weight: 500;
        }
    }
}