.card {
    &-csns {
        border-radius: 4px;
        overflow: hidden;

        .ratio {
            flex: 0 0 auto;
            width: 100%;
            background-color: #e9e7e7;
            margin-bottom: 20px;

            &:before {
                padding-top: 56%;
            }

            &>* {
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                max-width: 80%;
            }
        }

        &__item {

            &-header {
                .rating {
                    font-size: 24px;
                }
            }

            &-footer {
                padding: 10px 20px;

                .btn {
                    margin: 10px 0;
                    height: 50px;
                }
            }
        }
    }

    &-csn {
        border-radius: 4px;
        overflow: hidden;
        box-shadow: none;
        transition: box-shadow .3s ease-in-out;

        &:hover {
            box-shadow: 0 5px 10px rgba(0, 0, 0, .06);
        }

        &__left {
            border-right: 1px solid #e0e0e0;
            height: 100%;
            padding-bottom: 20px;

            &-img {
                background-color: #e9e7e7;
                margin-bottom: 20px;

                &:before {
                    padding-top: 100px;
                    @include media(768){
                        padding-top: 56%;
                    }
                }

                &>* {
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%)
                }
            }

            &-rating {
                flex: 1;
            }

            .rating {
                font-size: 24px;
            }
        }

        &__center {
            padding: 20px 20px 20px 0;
            border-right: 1px solid #e0e0e0;
            height: 100%;
            @include media(768){
                padding-top: 0;
                padding-left: 20px;
            }

            &-title {
                font-size: 24px;
                margin-bottom: 20px;
                @include media(768){
                    flex-direction: column-reverse;
                    .badge{
                        margin-bottom: 10px;
                    }
                }
            }
        }

        &__right {
            padding-right: 20px;
            height: 100%;
            @include media(768){
                padding-left: 20px;
                padding-bottom: 20px;
            }

            .btn {
                max-width: 220px;

                .icon {
                    stroke-width: 2;
                }
            }
        }
    }

    &-screen {
        border-radius: 4px;
        overflow: hidden;
        display: block;

        &>* {
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &-game {
        border-radius: 4px;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;

        &:hover {
            .card-game__wrapper {
                opacity: 1;
                transform: scale(1);
                pointer-events: all;
            }
        }

        &__wrapper {
            position: absolute;
            background-color: $white;
            width: 100%;
            border-radius: 4px;
            overflow: hidden;
            opacity: 0;
            transform: scale(0);
            transition: opacity .3s ease-in-out, transform .3s ease-in-out;
            z-index: 1;
            pointer-events: none;

            &-header {
                overflow: hidden;

                img {
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            &-footer {
                padding: 10px 20px;
                overflow: hidden;

                .btn {
                    margin: 10px 0;
                }
            }
        }

        &__header {
            border-top-left-radius: 4px;
            border-top-right-radius: 4px;
            overflow: hidden;

            img {
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__title {
            font-size: 20px;
            padding: 20px;
            display: flex;

            a {
                color: $Charcoal;
                font-weight: 700;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                display: block;
                line-height: 1.5;
            }

        }
    }

    &-games {
        &__category {
            background-color: $white;
            padding: 20px 0;
            color: $link;
            border: 1px solid #e0e0e0;
            position: relative;
            transition: border-color .2s ease-in-out, background-color .2s ease-in-out;
            &:hover{
                background-color: $AzureBlue;
                color: $white;
                border-color: $AzureBlue;
               
                span{
                    color: $white;
                }
            }

            &::before {
                content: "";
                width: 3px;
                background-color: #007bff;
                position: absolute;
                top: -1px;
                bottom: -1px;
                left: -1px;
                transition: width .3s ease-in-out;
            }

            svg {
                flex: 0 0 auto;
                width: 30px;
                max-width: 30px;
                height: 30px;
                margin-right: 10px;
                fill: currentColor;
                transition: all .2s ease-in-out;
            }

            span {
                font-weight: 700;
                color: $Charcoal;
                font-size: 18px;
                transition: color .2s ease-in-out;
            }
        }
    }
}