.menu {
    &-item{
        &__icon{
            flex: 0 0 auto;
            width: 20px;
            height: 20px;
            margin-right: 5px;
            @include media(1024){
                width: 24px;
                height: 24px;
                margin-right: 10px;
            }
            &-home{
                padding: 1px 0;
            }
            &-bonuses{
                padding: 1px 0;
            }
            &-news{
                padding: 2px 0;
            }
            .icon{
                width: 100%;
                height: 100%;
            }
        }
    }
    &-primary {
        >ul {
            height: 100%;
            @include media(1024){
                flex-direction: column;
                align-items: flex-start;
            }
            >li {

                &.current-menu-item,&:hover {
                    >a {

                        color: $GoldenYellow;
                        text-shadow: 0 0 6px rgba(255, 215, 0, 0.6);

                        &:after {
                            width: 100%;
                        }

                    }

                    
                }
                &:hover {
                    >.menu-sub{
                        opacity: 1;
                        transform: none;
                        pointer-events: all;
                        z-index: 10;
                    }
                }

                &:not(:last-child) {
                    margin-right: 20px;
                    @include media(1024){
                        margin-right: 0;
                        margin-bottom: 20px;
                    }
                }

                >a {
                    color: $white;
                    font-weight: 700;
                    transition: color .2s ease-in-out;
                    position: relative;
                    height: 100%;
                    @include media(1024){
                        font-size: 22px;
                    }

                    &:after {
                        content: "";
                        position: absolute;
                        left: 0;
                        bottom: 10px;
                        height: 2px;
                        background-color: $GoldenYellow;
                        width: 0;
                        transition: width .3s ease-in-out;
                        @include media(1024){
                            display: none;
                        }
                    }

                    &:hover {
                        color: $GoldenYellow;
                        text-shadow: 0 0 6px rgba(255, 215, 0, 0.6);

                        &:after {
                            width: 100%;
                        }
                    }

                    .icon {
                        fill: $GoldenYellow;
                    }
                }
            }
        }
    }

    &-sub {
        position: absolute;
        left: 0;
        width: 100%;
        opacity: 0;
        transform: translateY(30px);
        transition: opacity .2s ease-in-out, transform .2s ease-in-out;
        pointer-events: none;
        z-index: -1;
        @include media(1024){
            display: none;
        }

        &__wrap {
            background-color: $white;
            box-shadow: 0 5px 10px rgba(0, 0, 0, .06);
            padding: 60px;
            border-bottom-left-radius: 3px;
            border-bottom-right-radius: 3px;
            position: relative;
            .icon{
                width: 250px;
                height: 250px;
                position: absolute;
                bottom: 0;
                right: 0;
            }

            .title {
                font-weight: 700;
                display: block;
                margin-bottom: 16px;
                line-height: 1.4;
            }
            nav{
                ul{
                    li{
                        &:not(:last-child){
                            margin-bottom: 10px;
                        }
                        a{
                            color: $AzureBlue;
                            border-bottom: 1px solid transparent;
                            line-height: 1.5;
                            transition: border-color .2s ease-in-out;
                            font-size: 14px;
                            &:hover{
                                border-color: $AzureBlue;

                            }
                        }
                    }
                }
            }
        }
    }
}