.icon{
    &-20{
        width: 20px;
        max-width: 20px;
        height: 20px;
    }
    &-fill{
        fill: currentColor;
        transition: fill .2s ease-in-out;
    }
    &-stroke{
        fill: none;
        stroke: currentColor;
    }
    &-lang{
        &:before{
            padding-top: 100%;
        }
    }
    &-country{
        &:before{
            padding-top: 100%;
        }
    }
}